// src/components/Projects.js
import React from 'react';
import '../styles/Projects.css'; // Import your CSS file for styling
import project1 from '../assets/project1.png';
import project2 from '../assets/project2.png';
import project3 from '../assets/project3.png';
import project4 from '../assets/project4.png';
import project6 from '../assets/project6.png';
import projectpdf from '../assets/Virtual tour app- Portfolio Project 1 - Case study.pdf'

const projects = [
    {
        id: 1,
        title: "Menverse E-commerce Website",
        image: project1, // Replace with your image path
        description: "A full-stack e-commerce site developed using PHP and MySQL.",
        link: "https://github.com/itzzjb/menverse-ecommerce-website" // Replace with the link to the project
    },
    {
        id: 2,
        title: "Cucumber-Selenium Automated Testing Project",
        image: project2, // Replace with your image path
        description: "Developed an automated testing framework using Cucumber and Selenium.",
        link: "https://github.com/sewminiDeSilva/Cucumber-Selenium-Test" // Replace with the link to the project
    },
    {
        id: 3,
        title: "ToDo List Application",
        image: project3, // Replace with your image path
        description: "A React app for organizing daily tasks with ease.",
        link: "https://github.com/sewminiDeSilva/todo-list" // Replace with the link to the project
    },
    {
        id: 4,
        title: "Virtual Art Gallery",
        image: project4, // Replace with your image path
        description: "UI/UX case study design as part of the Google UI/UX course.",
        link: projectpdf // Replace with the link to the project
    },
    // {
    //     id: 5,
    //     title: "Weather App",
    //     image: "path/to/weather-app-image.jpg", // Replace with your image path
    //     description: "A simple weather application using React and OpenWeather API.",
    //     link: "https://github.com/sewminiDeSilva/weather-app" // Replace with the link to the project
    // },
    {
        id: 6,
        title: "Portfolio Website",
        image: project6, // Replace with your image path
        description: "My personal portfolio built with HTML, CSS and JavaScript.",
        link: "https://github.com/sewminiDeSilva/simple-portfolio" // Link to your portfolio
    }
];

const Projects = () => {
    return (
        <section id="projects">
            <h1>My Projects</h1>
            <div className="projects-container">
                {projects.map((project) => (
                    <div className="project-card" key={project.id}>
                        <img src={project.image} alt={project.title} className="project-image" />
                        <h2>{project.title}</h2>
                        <p>{project.description}</p>
                        <a href={project.link} className="see-more-button">See More</a>
                    </div>
                ))}
            </div>
        </section>
    );
};

export default Projects;